import {useState, useCallback, useEffect} from 'react'
import {categoricalColorSchemes} from '@nivo/colors'
import fetch from 'isomorphic-unfetch'
import { jStat } from 'jstat'
//import { scaleOrdinal } from 'd3-scale'
import { ResponsiveBar } from '@nivo/bar'

var steps = [
  '/foyer/demandeur',
  '/foyer/enfants',
  '/foyer/conjoint',
  '/foyer/logement',
  {
    angularjs:'/foyer/ressources/0/types',
    vuejs: '/foyer/demandeur/ressources/types',
  },
  {
    angularjs:'/foyer/ressources/0/montants',
    vuejs: '/foyer/demandeur/ressources/montants',
  },
  '/foyer/pensions-alimentaires',
  '/foyer/resultat'
]

function mappy(d) {
    return d.reduce((a, v) => {
        a[v.label] = v
        return a
    }, {})
}

let test = {
    angularjs: 'AngularJS',
    vuejs: 'VueJS'
}

test = {
    angularjs: 'period=day&date=2019-11-20',
    vuejs: 'period=day&date=2019-12-11'
}

const periodIndicator = [
    'period=week&date=2019-11-22',
    'period=day&date=yesterday',
    'period=week&date=yesterday',
    'period=day&date=2019-11-25',
    'period=day&date=2019-11-27',
    'period=day&date=2019-11-25',
    ][4]


function getDataUrl(key) {
    return `https://stats.data.gouv.fr/index.php?${periodIndicator}&filter_limit=100&flat=1&format=JSON&idSite=9&method=Actions.getPageUrls&module=API&segment=customVariableValue2%3D%3D${key}`
    return `https://stats.data.gouv.fr/index.php?${periodIndicator}&filter_limit=100&flat=1&format=JSON&idSite=9&method=Actions.getPageUrls&module=API&segment=customVariableValue2%3D%3D${key}`
}

function getDataUrlForPeriod(period) {
    return `https://stats.data.gouv.fr/index.php?${period}&filter_limit=100&flat=1&format=JSON&idSite=9&method=Actions.getPageUrls&module=API`
}

function getIndicator(p1, n1, p2, n2) {
    try {
        return jStat.fn.oneSidedDifferenceOfProportions(
            Math.min(1,p1), n1, Math.min(1, p2), n2
        )
    } catch(e) {
        console.error(e)
        return NaN
    }
}

function ABPAge() {
    const [values, setValues] = useState([]);
    async function fetchData() {
        try {
            const resA = await fetch(getDataUrlForPeriod(test.angularjs))
            const angularjs = mappy(await resA.json())
            
            const resB = await fetch(getDataUrlForPeriod(test.vuejs))
            const vuejs = mappy(await resB.json())
            console.log({angularjs, vuejs})

            const source = {angularjs, vuejs}
            const keys = ['angularjs', 'vuejs']

            const results = steps.reduce((accum, step) => {
                var values = keys.reduce((result, k) => {
                    const ref = source[k][step[k] || step]
                    const value = ref && (ref.sum_daily_nb_uniq_visitors || ref.nb_uniq_visitors) || 0
                    result[k + '-n'] = value
                    if (accum.previous[k] !== undefined) {
                        result[k + '-p'] = accum.previous[k] ? Math.min(1, value / accum.previous[k]) : 0
                        result[k + '-pr'] = Math.round(result[k + '-p'] * 1000)/10
                        result[k + '-prc'] = Math.round(result[k + '-n'] / accum.first[k] * 1000)/10
                    } else {
                        accum.first[k] = value
                    }
                    accum.previous[k] = value
                    return result
                }, {})

                const indicator = Math.round(getIndicator(
                    values['vuejs-p'],
                    values['vuejs-n'],
                    values['angularjs-p'],
                    values['angularjs-n'],
                ) * 1000) / 10
                accum.results.push({...values, indicator, step: (step.vuejs || step).slice(7)})

                return accum
            }, { results: [], previous: {}, first: {} }).results

            setValues(results)
        } catch (e) {
            console.error(e)
            setValues([])
        }
    }

    useEffect(() => {
      fetchData()
    }, [])

    const displayKeys = [
        {
            name: 'Conversions',
            keys: ['angularjs-pr', 'vuejs-pr', 'angularjs-prc', 'vuejs-prc'],
        },
        {
            name: 'Pertinence des Ã©cart',
            keys: ['indicator'],
        },
        {
            name: 'Nombre AngularJS',
            keys: ['angularjs-n'],
        },
        {
            name: 'Nombre VueJS',
            keys: ['vuejs-n'],
        }
    ]

    return (
        <div>
            <style jsx>{`
                .chart {
                    height: 300px;
                }
              `}</style>
            <h1>Stats A/B</h1>
            {displayKeys.map((d, i) => (
                <div>
                    <h2>{d.name}</h2>
                    <div className="chart" key={i}>
                        <ResponsiveBar
                            data={values}
                            keys={d.keys}
                            groupMode="grouped"
                            indexBy="step"
                            margin={{ top: 15, right: 10, bottom: 50, left: 60 }}
                            padding={0.3}
                            borderColor={{ from: 'color', modifiers: [ [ 'darker', 1.6 ] ] }}
                            animate={false}
                        />
                    </div>
                </div>
            ))}
            <pre>{JSON.stringify(values, null, 2)}</pre>
        </div>
    );
}

export default ABPAge;
